import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { postGame } from "../../api /user/postGame";
import { Button } from "../../components/Button/Button";
import GameDone from "../../components/GameDone/GameDone";
import Header from "../../components/Header/Header";
import CheckListGame from "../../games/CheckListGame/CheckListGame";
import PhrazleGame from "../../games/PhrazleGame/PhrazleGame";
import SwiperChoice from "../../games/SwiperChoiceGame/SwiperChoiceGame";
import TestGame from "../../games/TestGame/TestGame";
import { useCookies } from "../../hooks/useCookies";
import { useAppDispatch, useAppSelector } from "../../store";
import {
    completeGame,
    GameType,
    selectDays,
    selectIsLogin,
    setCurrentModal,
} from "../../store/main.reducer";
import css from "./GamePage.module.css";

export interface GamePageProps {}

export default function GamePage(props: GamePageProps) {
    const days = useAppSelector(selectDays);
    const { state } = useLocation();
    const { dayID, gameID } = state;
    const currentDay = days.find((day) => day.id === dayID);
    const currentGame = currentDay?.games?.find((game) => game.id === gameID);
    const dispatch = useAppDispatch();
    const { getCookie, setCookie } = useCookies();
    const cookieSessionID = getCookie("sessionID");
    const isLogin = useAppSelector(selectIsLogin);

    const checkGame = async (dayID: number, gameID: number) => {
        dispatch(completeGame({ dayID: dayID, gameID: gameID }));
        if (isLogin) {
            postGame(gameID, cookieSessionID as string);
        } else {
            const doneGamesIDs = currentDay?.games
                ?.filter((game) => game.done)
                .map((game) => game.id);
            setCookie(
                "unlogin_progress",
                doneGamesIDs ? [...doneGamesIDs, gameID] : [gameID]
            );
        }
    };

    const gamesCatalog = [
        {
            type: GameType.TEST,
            component: (
                <TestGame
                    game={currentGame}
                    dayID={currentDay?.id}
                    gameID={currentGame?.id}
                    checkGame={checkGame}
                />
            ),
        },
        {
            type: GameType.CHECKLIST,
            component: (
                <CheckListGame
                    game={currentGame}
                    dayID={currentDay?.id}
                    gameID={currentGame?.id}
                    checkGame={checkGame}
                />
            ),
        },
        {
            type: GameType.PHRAZLE,
            component: (
                <PhrazleGame
                    game={currentGame}
                    dayID={currentDay?.id}
                    gameID={currentGame?.id}
                    correctWord="СЕЙЧАС"
                    availableLetters={["С", "С", "Й", "А", "Ч", "Е"]}
                    checkGame={checkGame}
                />
            ),
        },
        {
            type: GameType.SWIPER_CHOICE,
            component: (
                <SwiperChoice
                    game={currentGame}
                    dayID={currentDay?.id}
                    gameID={currentGame?.id}
                    checkGame={checkGame}
                />
            ),
        },
    ];

    const currentGameFromCatalog = gamesCatalog.find(
        (game) => game.type === currentGame?.type
    );

    useEffect(() => {
        dispatch(setCurrentModal("game"));
    }, []);

    return (
        <div className={css["main"]}>
            <Header>
                <div className={css["game-header"]}>
                    <img
                        className={css["game-header__pic"]}
                        width={97}
                        src={currentGame?.picHeader}
                        alt=""
                    />
                    <Button size="small" className={css["game-header__button"]}>
                        {currentGame?.title}
                    </Button>
                </div>
            </Header>
            <main className={css["main-content"]}>
                <div className="container">
                    {currentGame?.done ? (
                        <GameDone game={currentGame} />
                    ) : (
                        currentGameFromCatalog?.component
                    )}
                    {}
                </div>
            </main>
        </div>
    );
}
