export const transformProgressData = (apiData: ApiDay[]): Day[] => {
    return apiData.map((apiDay, index) => {
        const dayNumber = parseInt(apiDay.day_number);

        // Определяем доступность дня
        const isActive =
            apiDay.day_status === "Open" ||
            apiDay.day_status === "Open Penalty Complete" ||
            apiDay.day_status === "End Success";
        const isPenalty = apiDay.day_status === "Open Penalty";
        const isDone = apiDay.day_complete_time !== "";

        // Преобразуем игры
        const games: Game[] = apiDay.day_games.map((apiGame, gameIndex) => {
            const gameConfig = gameConfigs[apiGame.game_title] || {};

            return {
                id: gameIndex + 1,
                title: apiGame.game_title,
                pic: `assets/images/days/day${dayNumber}/day${dayNumber}game${
                    gameIndex + 1
                }.png`,
                picHeader: `assets/images/days/day${dayNumber}/day${dayNumber}game${
                    gameIndex + 1
                }header.png`,
                available: isActive, // Игра доступна, если день активен
                done: apiGame.game_completed,
                type: GameType.TEST, // По умолчанию
                complete: {
                    pic: "assets/images/game-done-check.png",
                    title: "Задание выполнено",
                    text: "",
                },
                description: "Описание игры",
                ...gameConfig,
                // Добавляем тестовые данные если это тест
                ...(gameConfig.type === GameType.TEST && {
                    test:
                        testData[apiGame.game_title as keyof typeof testData] ||
                        [],
                }),
            } as Game;
        });

        return {
            id: dayNumber,
            pic: "assets/images/prizes/prize-litres.png",
            name: apiDay.day_title,
            active: isActive,
            done: isDone,
            penalty: isPenalty,
            type: dayTypeMap[apiDay.day_title] || DayType.CHOCOLATE,
            icon:
                `assets/images/${dayTypeMap[
                    apiDay.day_title
                ]?.toLowerCase()}-icon.png` ||
                "assets/images/chocolate-icon.png",
            time: `${apiDay.day_open_date}T00:00:00+03:00`,
            games: games,
        } as Day;
    });
};
