// components/Modals/ResetPasswordModal.tsx
import classNames from "classnames";
import React, { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import ArrowRightIcon from "../../icons/ArrowRightIcon/ArrowRightIcon";
import { useAppDispatch } from "../../store";
import { setCurrentModal } from "../../store/main.reducer";
import CommonModal from "../CommonModal/CommonModal";
import css from "../modals.module.css";
import { useCookies } from "../../hooks/useCookies";
import { resetUserPassword } from "../../api /user/resetUserPassword";
import { Text } from "../../components/Text/Text";
import { transformFetchError } from "../../helpers/transformFetchError";

interface ResetPasswordModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const ResetPasswordModal: React.FC<ResetPasswordModalProps> = ({
    isOpen,
    onClose,
}) => {
    const dispatch = useAppDispatch();
    const [email, setEmail] = useState("");
    const [isSend, setIsSend] = useState(false);
    const [touched, setTouched] = useState(false);
    const [error, setError] = useState("");
    const { getCookie } = useCookies();
    const cookieSessionID = getCookie("sessionID");
    const resetPasswordHandler = async () => {
        try {
            const resetUserPasswordResponse = await resetUserPassword(
                email as string
            );
            setIsSend(true);
        } catch (e: any) {
            setError(
                transformFetchError(e.response.data.data.error_type as string)
            );
            console.log(e);
        }
    };

    const handleSubmit = () => {
        setTouched(true);

        // Валидация
        if (!email) {
            setError("Email обязателен");
            return;
        }

        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
            setError("Неверный формат email");
            return;
        }

        setError("");
        resetPasswordHandler();
    };

    const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setEmail(value);

        // Валидация на лету после первого касания
        if (touched) {
            if (!value) {
                setError("Email обязателен");
            } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
                setError("Неверный формат email");
            } else {
                setError("");
            }
        }
    };

    const handleEmailBlur = () => {
        setTouched(true);
        // Валидация при потере фокуса
        if (!email) {
            setError("Email обязателен");
        } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
            setError("Неверный формат email");
        } else {
            setError("");
        }
    };

    const isFormValid = email && /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);

    return (
        <CommonModal isOpen={isOpen} onClose={onClose} modalKey="resetPassword">
            {isSend ? (
                <div className={classNames(css["user-sent"])}>
                    <img
                        width={60}
                        height={60}
                        src="assets/images/email-icon.svg"
                    />
                    <Title size="normal">Подтвердите сброс</Title>
                    <Text>
                        На email{" "}
                        <span style={{ color: "#35A2EB" }}>{email}</span>, было
                        отправлено письмо с ссылкой на сброс пароля, перейдите
                        по ней, чтобы изменить ваш пароль
                    </Text>
                </div>
            ) : (
                <div className={classNames(css["user-modal"])}>
                    <Title className={css["user-modal__title"]}>
                        Сброс пароля
                    </Title>
                    <form
                        onSubmit={(e) => e.preventDefault()}
                        className={classNames(
                            css["user-modal__form"],
                            css["form"]
                        )}
                        noValidate
                    >
                        <div className={css["form-input"]}>
                            <label
                                className={css["form-input__label"]}
                                htmlFor="email"
                            >
                                Email
                            </label>
                            <input
                                id="email"
                                name="email"
                                type="email"
                                value={email}
                                onChange={handleEmailChange}
                                onBlur={handleEmailBlur}
                                className={classNames(
                                    css["input"],
                                    css["input_type_email"],
                                    {
                                        [css["input_error"]]: touched && error,
                                    }
                                )}
                                placeholder="Начните вводить..."
                            />
                            {error && (
                                <span className={css["error-message"]}>
                                    {error}
                                </span>
                            )}
                        </div>

                        <div className={css["user-modal__buttons"]}>
                            <Button
                                color="outline-blue"
                                className={css["user-modal__button"]}
                                onClick={() =>
                                    dispatch(setCurrentModal("signup"))
                                }
                            >
                                Создать аккаунт
                            </Button>
                            <Button
                                className={css["user-modal__button"]}
                                onClick={handleSubmit}
                                disabled={!isFormValid}
                            >
                                Сбросить
                                <ArrowRightIcon />
                            </Button>
                        </div>
                    </form>
                </div>
            )}
        </CommonModal>
    );
};

export default ResetPasswordModal;
