import React from "react";
import Modal from "react-modal";
import { motion } from "framer-motion";
import css from "../modals.module.css";
import CloseIcon from "../../icons/CloseIcon/CloseIcon";
import { useAppSelector } from "../../store";
import { selectCurrentModal } from "../../store/main.reducer";
import classNames from "classnames";

Modal.setAppElement("#root");

interface CommonModalProps {
    isOpen: boolean;
    onClose: () => void;
    children: React.ReactNode;
    modalKey: string;
    className?: string;
}

const CommonModal: React.FC<CommonModalProps> = ({
    isOpen,
    onClose,
    children,
    modalKey,
    className,
}) => {
    const modalVariants = {
        hidden: {
            opacity: 0,
            scale: 0.8,
            y: -50,
        },
        visible: {
            opacity: 1,
            scale: 1,
            y: 0,
            transition: {
                duration: 0.3,
                // ease: "easeInOut",
            },
        },
        exit: {
            opacity: 0,
            scale: 0.8,
            y: 50,
            transition: {
                duration: 0.2,
            },
        },
    };

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onClose}
            className={classNames(css["modal"], className)}
            overlayClassName={css["modal-overlay"]}
            // closeTimeoutMS={300}
        >
            <button className={css["modal__close"]} onClick={onClose}>
                <CloseIcon />
            </button>
            <motion.div
                key={modalKey}
                initial="hidden"
                animate="visible"
                exit="exit"
                variants={modalVariants}
                className="modal-content"
            >
                {children}
            </motion.div>
        </Modal>
    );
};

export default CommonModal;
