import Cookies from "js-cookie";
import { useCallback, useState } from "react";

export const useCookies = <T>() => {
    const [value, setValue] = useState<T | null>(null);

    const setCookie = useCallback(
        (name: string, data: T, options?: Cookies.CookieAttributes) => {
            const stringValue =
                typeof data === "string" ? data : JSON.stringify(data);
            Cookies.set(name, stringValue, options);
            setValue(data);
        },
        []
    );

    const getCookie = useCallback((name: string): T | null => {
        const cookieValue = Cookies.get(name);
        if (!cookieValue) return null;

        try {
            return JSON.parse(cookieValue) as T;
        } catch {
            return cookieValue as unknown as T;
        }
    }, []);

    const removeCookie = useCallback(
        (name: string, options?: Cookies.CookieAttributes) => {
            Cookies.remove(name, options);
            setValue(null);
        },
        []
    );

    return { value, setCookie, getCookie, removeCookie };
};
