import { ReactNode } from "react";
import css from "./Text.module.css";
import classNames from "classnames";


export type Text = {
    children?: ReactNode;
    className?: string;
    color?: "yellow" | "gray" | "white" | "black";
    size?: "normal" | "small";
    align?: "left" | "center" | "small";
};

export const Text = ({
    children,
    className,
    color = 'yellow',
    size = 'normal',
    align = 'center',
}: Text) => {
    return (
        <div
            className={classNames(
                css["text"],
                css[`color_type_${color}`],
                css[`size_type_${size}`],
                css[`align_type_${align}`],
                className
            )}
        >
            {children}
        </div>
    );
};
