import classNames from "classnames";
import React, { useState } from "react";
import { signupUser } from "../../api /user/signupUser";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import { useRegistrationForm } from "../../hooks/useRegistrationForm";
import ArrowRightIcon from "../../icons/ArrowRightIcon/ArrowRightIcon";
import { useAppDispatch } from "../../store";
import { setCurrentModal } from "../../store/main.reducer";
import CommonModal from "../CommonModal/CommonModal";
import css from "../modals.module.css";
import { Text } from "../../components/Text/Text";
import { transformFetchError } from "../../helpers/transformFetchError";
import { useCookies } from "../../hooks/useCookies";

interface SignUpModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const SignUpModal: React.FC<SignUpModalProps> = ({ isOpen, onClose }) => {
    const dispatch = useAppDispatch();
    const [email, setEmail] = useState("");
    const [password1, setPassword1] = useState("");
    const [password2, setPassword2] = useState("");
    const [isSend, setIsSend] = useState(false);
    const [fetchError, setFetchError] = useState("");
    const { getCookie } = useCookies();

    const { formData, errors, touched, handleChange, handleBlur, isFormValid } =
        useRegistrationForm();

    const submitRegistration = async () => {
        try {
            const unloginProgress = getCookie("unlogin_progress");
            const signUpUserResponse = await signupUser(
                email,
                password1,
                unloginProgress && Array.isArray(unloginProgress)
                    ? [...unloginProgress]
                    : []
            );
            setIsSend(true);
            // const responseSessionID = signUpUserResponse.data.user_id;
        } catch (e: any) {
            console.log(e);
            setFetchError(
                transformFetchError(e.response.data.data.error_type as string)
            );
        }
    };

    return (
        <CommonModal isOpen={isOpen} onClose={onClose} modalKey="login">
            {isSend ? (
                <div className={classNames(css["user-sent"])}>
                    <img
                        width={60}
                        height={60}
                        src="assets/images/email-icon.svg"
                    />
                    <Title size="normal">Подтвердите свой Email</Title>
                    <Text>
                        На email{" "}
                        <span style={{ color: "#35A2EB" }}>{email}</span>, было
                        отправлено письмо с ссылкой на подтверждение почтового
                        адреса, перейдите по ней, чтобы создать аккаунт
                    </Text>
                </div>
            ) : (
                <div className={classNames(css["user-modal"])}>
                    <Title className={css["user-modal__title"]}>
                        Регистрация
                    </Title>
                    <form
                        className={classNames(
                            css["user-modal__form"],
                            css["form"]
                        )}
                        onSubmit={(e) => e.preventDefault()}
                    >
                        <div className={css["form-input"]}>
                            <label
                                className={css["form-input__label"]}
                                htmlFor="email"
                            >
                                Email
                            </label>
                            <input
                                id="email"
                                name="email"
                                type="email"
                                value={formData.email}
                                onChange={(e) => {
                                    setEmail(e.target.value);
                                    handleChange(e);
                                    setFetchError("");
                                }}
                                onBlur={handleBlur}
                                className={classNames(
                                    css["input"],
                                    css["input_type_email"],
                                    {
                                        [css["input_error"]]:
                                            touched.email && errors.email,
                                    }
                                )}
                                placeholder="Начните вводить..."
                            />
                            {touched.email && errors.email && (
                                <span className={css["error-message"]}>
                                    {errors.email}
                                </span>
                            )}
                        </div>
                        <div className={css["form-inputs"]}>
                            <div className={css["form-input"]}>
                                <label
                                    className={css["form-input__label"]}
                                    htmlFor="password"
                                >
                                    Пароль
                                </label>
                                <input
                                    id="password"
                                    name="password"
                                    type="password"
                                    value={formData.password}
                                    onChange={(e) => {
                                        setPassword1(e.target.value);
                                        handleChange(e);
                                        setFetchError("");
                                    }}
                                    onBlur={handleBlur}
                                    className={classNames(
                                        css["input"],
                                        css["input_type_password"],
                                        {
                                            [css["input_error"]]:
                                                touched.password &&
                                                errors.password,
                                        }
                                    )}
                                    placeholder="Начните вводить..."
                                />
                                {touched.password && errors.password && (
                                    <span className={css["error-message"]}>
                                        {errors.password}
                                    </span>
                                )}
                            </div>

                            <div className={css["form-input"]}>
                                <label
                                    className={css["form-input__label"]}
                                    htmlFor="confirmPassword"
                                >
                                    Повторите пароль
                                </label>
                                <input
                                    id="confirmPassword"
                                    name="confirmPassword"
                                    type="password"
                                    value={formData.confirmPassword}
                                    onChange={(e) => {
                                        setPassword2(e.target.value);
                                        handleChange(e);
                                        setFetchError("");
                                    }}
                                    onBlur={handleBlur}
                                    className={classNames(
                                        css["input"],
                                        css["input_type_password"],
                                        {
                                            [css["input_error"]]:
                                                touched.confirmPassword &&
                                                errors.confirmPassword,
                                        }
                                    )}
                                    placeholder="Начните вводить..."
                                />
                                {touched.confirmPassword &&
                                    errors.confirmPassword && (
                                        <span className={css["error-message"]}>
                                            {errors.confirmPassword}
                                        </span>
                                    )}
                            </div>
                        </div>

                        <div className={css["form__policies"]}>
                            <div
                                className={classNames(css["policy-checkbox"], {
                                    [css["checkbox_error"]]:
                                        touched.rules && errors.rules,
                                })}
                            >
                                <input
                                    type="checkbox"
                                    id="rules"
                                    name="rules"
                                    checked={formData.rules}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                />
                                <label htmlFor="rules">
                                    Cогласен с <a href="#">правилами акции</a>
                                </label>
                                {touched.rules && errors.rules && (
                                    <span className={css["error-message"]}>
                                        {errors.rules}
                                    </span>
                                )}
                            </div>

                            <div
                                className={classNames(css["policy-checkbox"], {
                                    [css["checkbox_error"]]:
                                        touched.conditions && errors.conditions,
                                })}
                            >
                                <input
                                    type="checkbox"
                                    id="conditions"
                                    name="conditions"
                                    checked={formData.conditions}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                />
                                <label htmlFor="conditions">
                                    Я прочитал и принял{" "}
                                    <a href="#">условия конфиденциальности</a>
                                    <br />и даю свое согласие на обработку
                                    персональных данных
                                </label>
                                {touched.conditions && errors.conditions && (
                                    <span className={css["error-message"]}>
                                        {errors.conditions}
                                    </span>
                                )}
                            </div>

                            <div className={css["policy-checkbox"]}>
                                <input
                                    type="checkbox"
                                    id="mailing"
                                    name="mailing"
                                    checked={formData.mailing}
                                    onChange={handleChange}
                                />
                                <label htmlFor="mailing">
                                    Я согласен получать рассылки от Киндер
                                </label>
                            </div>
                        </div>
                        {fetchError && (
                            <span className={css["error-message"]}>
                                <br />
                                {fetchError}
                            </span>
                        )}
                        <div className={css["user-modal__buttons"]}>
                            <Button
                                color="outline-blue"
                                className={css["user-modal__button"]}
                                onClick={() =>
                                    dispatch(setCurrentModal("login"))
                                }
                            >
                                У меня есть аккаунт
                            </Button>
                            <Button
                                className={css["user-modal__button"]}
                                onClick={submitRegistration}
                                disabled={!isFormValid}
                            >
                                Создать
                                <ArrowRightIcon />
                            </Button>
                        </div>
                    </form>
                </div>
            )}
        </CommonModal>
    );
};

export default SignUpModal;
