import React from "react";
import { useAppDispatch, useAppSelector } from "../../store";
import { selectCurrentModal, setCurrentModal } from "../../store/main.reducer";
import LoginModal from "../LoginModal/LoginModal";
import ResetPasswordModal from "../ResetPasswordModal/ResetPasswordModal";
import SignUpModal from "../SignUpModal/SignUpModal";
import PenaltyModal from "../PenaltyModal/PenaltyModal";
import GameModal from "../GameModal/GameModal";

const ModalManager: React.FC = () => {
    const currentModal = useAppSelector(selectCurrentModal);
    const dispatch = useAppDispatch();
    const onClose = () => {
        dispatch(setCurrentModal(null));
    };

    return (
        <>
            <LoginModal isOpen={currentModal === "login"} onClose={onClose} />
            <SignUpModal isOpen={currentModal === "signup"} onClose={onClose} />
            <ResetPasswordModal isOpen={currentModal === "reset"} onClose={onClose} />
            <PenaltyModal isOpen={currentModal === "penalty"} onClose={onClose} />
            <GameModal isOpen={currentModal === "game"} onClose={onClose} />
        </>
    );
};

export default ModalManager;
