import classNames from "classnames";
import React, { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Text } from "../../components/Text/Text";
import { Title } from "../../components/Title/Title";
import { useAppDispatch, useAppSelector } from "../../store";
import CommonModal from "../CommonModal/CommonModal";
import css from "../modals.module.css";
import { useLocation } from "react-router-dom";
import { selectDays } from "../../store/main.reducer";

interface GameModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const GameModal: React.FC<GameModalProps> = ({ isOpen, onClose }) => {
    const { state } = useLocation();
    const { dayID = null, gameID = null } = state || {};
    const days = useAppSelector(selectDays);
    const currentDay = days.find((day) => day.id === dayID);
    const currentGame = currentDay?.games?.find((game) => game.id === gameID);

    return (
        <CommonModal
            className={css["game-modal"]}
            isOpen={isOpen}
            onClose={onClose}
            modalKey="resetPassword"
        >
            <div className={classNames(css["game-modal__content"])}>
                <div className={css["game-modal-header"]}>
                    <img
                        className={css["game-modal-header__pic"]}
                        width={97}
                        src={currentGame?.picHeader}
                        alt=""
                    />
                    <Button
                        size="small"
                        className={css["game-modal-header__button"]}
                    >
                        {currentGame?.title}
                    </Button>
                </div>
                <Text className={css["game-modal__text"]}>
                    {currentGame?.description}
                </Text>
                <Button
                    color="golden"
                    className={css["game-modal__button"]}
                    onClick={onClose}
                >
                    Начать
                </Button>
            </div>
        </CommonModal>
    );
};

export default GameModal;
