import * as React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import BackArrowIcon from "../../icons/BackArrowIcon/BackArrowIcon";
import ProfileIcon from "../../icons/ProfileIcon/ProfileIcon";
import { useAppDispatch, useAppSelector } from "../../store";
import {
    selectIsLogin,
    selectUserEmail,
    setCurrentModal,
} from "../../store/main.reducer";
import css from "./Header.module.css";
import { Button } from "../Button/Button";
import { Text } from "../Text/Text";
import { useCookies } from "../../hooks/useCookies";

export interface HeaderProps {
    children?: React.ReactNode;
}

export default function Header(props: HeaderProps) {
    const location = useLocation();
    const isMainPage = location.pathname === "/";
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const isLogin = useAppSelector(selectIsLogin);
    const userEmail = useAppSelector(selectUserEmail);
    const [isMenuOpen, setIsMenuOpen] = React.useState(false);
    const { setCookie } = useCookies();

    const logOut = async () => {
        setCookie("sessionID", "");
        navigate("/");
        window.location.reload();
    };

    return (
        <header className={css["header"]}>
            <div className="container">
                <nav className={css["header-nav"]}>
                    <button
                        className={css["header__back"]}
                        onClick={() => navigate(-1)}
                    >
                        <BackArrowIcon />
                    </button>
                    {props.children ? (
                        props.children
                    ) : (
                        <img
                            src="assets/images/maxi-king-logo.png"
                            width={97}
                            height={74}
                            className={css["header__logo"]}
                        />
                    )}

                    {isLogin && userEmail ? (
                        <div className={css["user-wrapper"]}>
                            <button
                                className={css["user-button"]}
                                onClick={() => setIsMenuOpen(!isMenuOpen)}
                            >
                                <ProfileIcon fill="#35A2EB" />
                                <span>{userEmail}</span>
                            </button>
                            {isMenuOpen && (
                                <div className={css["user-menu"]}>
                                    <Text
                                        size="small"
                                        className={css["user-menu__email"]}
                                    >
                                        <b>{userEmail}</b>
                                    </Text>
                                    <Button
                                        size="small"
                                        className={css["user-menu__button"]}
                                        onClick={() => navigate("/")}
                                    >
                                        Игры
                                    </Button>
                                    <Button
                                        size="small"
                                        className={css["user-menu__button"]}
                                        color="white"
                                    >
                                        Мои призы
                                    </Button>
                                    <Button
                                        size="small"
                                        className={css["user-menu__button"]}
                                        color="outline-gray"
                                        onClick={logOut}
                                    >
                                        Выйти
                                    </Button>
                                </div>
                            )}
                        </div>
                    ) : (
                        <button
                            className={css["login-button"]}
                            onClick={() => dispatch(setCurrentModal("login"))}
                        >
                            <ProfileIcon />
                            <span>Войти</span>
                        </button>
                    )}
                </nav>
            </div>
        </header>
    );
}
