import classNames from "classnames";
import React, { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Text } from "../../components/Text/Text";
import { Title } from "../../components/Title/Title";
import { useAppDispatch } from "../../store";
import CommonModal from "../CommonModal/CommonModal";
import css from "../modals.module.css";
import { postGame } from "../../api /user/postGame";
import { useCookies } from "../../hooks/useCookies";
import { getProgress } from "../../api /user/getProgress";
import { setDays } from "../../store/main.reducer";
import { transformProgressData } from "../../helpers/transformProgressData";

interface PenaltyModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const PenaltyModal: React.FC<PenaltyModalProps> = ({ isOpen, onClose }) => {
    const dispatch = useAppDispatch();
    const [isPaid, setIsPaid] = useState(false);
    const { getCookie } = useCookies();

    const payPenalty = async () => {
        try {
            const cookieSessionID = getCookie("sessionID");
            const postGameResponse = await postGame(
                26,
                cookieSessionID as string,
                true
            );
            setIsPaid(true);
            const progressResponse = await getProgress(
                cookieSessionID as string
            );
            console.log(progressResponse);
            dispatch(
                setDays(
                    transformProgressData(
                        progressResponse?.data?.user_progression
                    )
                )
            );
        } catch (e) {
            console.log(e);
        }
    };

    return (
        <CommonModal
            className={css["penalty-modal"]}
            isOpen={isOpen}
            onClose={onClose}
            modalKey="resetPassword"
        >
            <div className={classNames(css["penalty-modal__content"])}>
                {!isPaid ? (
                    <>
                        <div className={css["penalty-header"]}>
                            <img
                                className={css["penalty-header__pic"]}
                                width={97}
                                src={"assets/images/penalty-pic.png"}
                                alt=""
                            />
                            <Button
                                size="small"
                                className={css["penalty-header__button"]}
                            >
                                Штраф от Завтра
                            </Button>
                        </div>
                        <Text className={css["penalty-modal__text"]}>
                            Напишите в заметки:
                        </Text>
                        <Title
                            className={css["penalty-modal__title"]}
                            size="normal"
                        >
                            Я торжественно обещаю отложить всё ещё на денёк,
                            если не возьмусь за дело сегодня
                        </Title>
                        <Button
                            color="golden"
                            className={css["penalty-modal__button"]}
                            onClick={payPenalty}
                        >
                            Я сделал это
                        </Button>
                    </>
                ) : (
                    <div className={css["penalty-paid"]}>
                        <img
                            className={css["penalty-paid__image"]}
                            width={190}
                            src="assets/images/game-done-check.png"
                            alt=""
                        />
                        <Title
                            className={css["penalty-modal__title"]}
                            size="normal"
                        >
                            Формально всё честно: обещал — значит сделал. <br />
                            Даже если сделал новую отложку
                        </Title>
                        <Button
                            color="golden"
                            className={css["penalty-modal__button"]}
                            onClick={onClose}
                        >
                            К играм!
                        </Button>
                    </div>
                )}
            </div>
        </CommonModal>
    );
};

export default PenaltyModal;
