import { ReactNode } from "react";
import css from "./Button.module.css";
import classNames from "classnames";

export const Button = ({
    onClick,
    children,
    color = "orange",
    href,
    disabled,
    size = 'normal',
    className,
}: {
    onClick?: () => void;
    children: ReactNode;
    color?: "orange" | "white" | "transparent"  | "blue" | "golden" | "brown" | "outline" | "outline-blue" | 'outline-gray';
    size?: "normal" | "small" | "big";
    href?: "string";
    disabled?: boolean;
    className?: string;
}) => {
    if (href) {
        return (
            <a
                onClick={onClick}
                href={href}
                className={classNames(
                    css["button"],
                    color && css[`button_color_${color}`],
                    size && css[`button_size_${size}`],
                    className && className
                )}
                target="_blank"
            >
                <span>{children}</span>
            </a>
        );
    } else {
        return (
            <button
                disabled={disabled}
                onClick={onClick}
                className={classNames(
                    css["button"],
                    color && css[`button_color_${color}`],
                    size && css[`button_size_${size}`],
                    className && className
                )}
            >
                <span>{children}</span>
            </button>
        );
    }
};
