import { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import { completeGame, Game } from "../../store/main.reducer";
import css from "./TestGame.module.css";
import { useAppDispatch } from "../../store";

export interface TestGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function TestGame({
    game,
    dayID,
    gameID,
    checkGame,
}: TestGameProps) {
    // const dispatch = useAppDispatch();
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const test = game?.test;
    const [answers, setAnswers] = useState<number[]>([]);
    const [currentAnswer, setCurrentAnswer] = useState(0);

    const submitAnswer = () => {
        setAnswers([...answers, currentAnswer]);
        setCurrentQuestion(currentQuestion + 1);
        if (test && currentQuestion + 1 < test?.length) {
            setCurrentQuestion(currentQuestion + 1);
            setCurrentAnswer(0);
        } else {
            checkGame(dayID ? dayID : 0, gameID ? gameID : 0);
            // dispatch(completeGame({ dayID: dayID, gameID: gameID }));
        }
    };

    return (
        <div className={css["test-game"]}>
            <div className={css["test-game__counter"]}>
                {currentQuestion + 1}/{test?.length}
            </div>
            {test && test.length > 0 && (
                <>
                    <Title className={css["test-game__title"]}>
                        {test[currentQuestion].question}
                    </Title>
                    <div className={css["test-game__answers"]}>
                        {test[currentQuestion].answers.map((answer) => (
                            <Button
                                key={answer.id}
                                color={
                                    currentAnswer === answer.id
                                        ? "orange"
                                        : "brown"
                                }
                                onClick={() => setCurrentAnswer(answer.id)}
                                className={css["test-game__answer"]}
                            >
                                {answer.text}
                            </Button>
                        ))}
                    </div>
                    <Button
                        className={css["test-game__submit"]}
                        color="golden"
                        disabled={currentAnswer === 0}
                        onClick={submitAnswer}
                    >
                        Далее
                    </Button>
                </>
            )}
        </div>
    );
}
