import { ReactNode } from "react";
import css from "./Title.module.css";
import classNames from "classnames";


export type Title = {
    children?: ReactNode;
    className?: string;
    color?: "yellow" | "grey" | "white" | "black";
    size?: "normal" | "small" | "big";
    align?: "left" | "center" | "small";
};

export const Title = ({
    children,
    className,
    color = 'black',
    size = 'big',
    align = 'center',
}: Title) => {
    return (
        <div
            className={classNames(
                css["title"],
                css[`color_type_${color}`],
                css[`size_type_${size}`],
                css[`align_type_${align}`],
                className
            )}
        >
            {children}
        </div>
    );
};
