export const transformFetchError = (error: string): string => {
    const errorMap: Record<string, string> = {
        already_exists: "Аккаунт с этой почтой уже зарегистрирован.",
        invalid_login_or_pass: "Неверный email или пароль.",
        not_found_error: "Неверный email.",
    };

    if (errorMap[error]) {
        return errorMap[error];
    } else {
        return "";
    }
};
