// components/Modals/LoginModal.tsx
import classNames from "classnames";
import React, { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import ArrowRightIcon from "../../icons/ArrowRightIcon/ArrowRightIcon";
import { useAppDispatch } from "../../store";
import {
    setCurrentModal,
    setIsLogin,
    setUserEmail,
} from "../../store/main.reducer";
import CommonModal from "../CommonModal/CommonModal";
import css from "../modals.module.css";
import { loginUser } from "../../api /user/loginUser";
import { useCookies } from "../../hooks/useCookies";
import { useLoginForm } from "../../hooks/useLoginForm";
import { transformFetchError } from "../../helpers/transformFetchError";
import { useNavigate } from "react-router-dom";

interface LoginModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose }) => {
    const dispatch = useAppDispatch();
    const [fetchError, setFetchError] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const { setCookie, getCookie } = useCookies();
    const {
        formData,
        errors,
        touched,
        isFormValid,
        handleChange,
        handleBlur,
        handleSubmit,
    } = useLoginForm();

    const cookie = getCookie("sessionID");
    const navigate = useNavigate();

    const loginHandler = async () => {
        try {
            const loginResponse = await loginUser(email, password);
            console.log(loginResponse);
            setCookie("sessionID", loginResponse.data.session_id);
            navigate("/");
            window.location.reload();
        } catch (e: any) {
            console.log(e);
            setFetchError(
                transformFetchError(e.response.data.data.error_type as string)
            );
        }
    };

    return (
        <CommonModal isOpen={isOpen} onClose={onClose} modalKey="login">
            <div className={classNames(css["user-modal"])}>
                <Title className={css["user-modal__title"]}>Авторизация</Title>
                <form
                    onSubmit={handleSubmit(loginHandler)}
                    className={classNames(css["user-modal__form"], css["form"])}
                    noValidate
                >
                    <div className={css["form-input"]}>
                        <label
                            className={css["form-input__label"]}
                            htmlFor="email"
                        >
                            Email
                        </label>
                        <input
                            id="email"
                            name="email"
                            type="email"
                            value={formData.email}
                            onChange={(e) => {
                                handleChange(e);
                                setEmail(e.target.value);
                                setFetchError("");
                            }}
                            onBlur={handleBlur}
                            className={classNames(
                                css["input"],
                                css["input_type_email"],
                                {
                                    [css["input_error"]]:
                                        touched.email && errors.email,
                                }
                            )}
                            placeholder="Начните вводить..."
                        />
                        {touched.email && errors.email && (
                            <span className={css["error-message"]}>
                                {errors.email}
                            </span>
                        )}
                    </div>

                    <div className={css["form-input"]}>
                        <label
                            className={css["form-input__label"]}
                            htmlFor="password"
                        >
                            Пароль
                        </label>
                        <input
                            id="password"
                            name="password"
                            type="password"
                            value={formData.password}
                            onChange={(e) => {
                                handleChange(e);
                                setPassword(e.target.value);
                                setFetchError("");
                            }}
                            onBlur={handleBlur}
                            className={classNames(
                                css["input"],
                                css["input_type_password"],
                                {
                                    [css["input_error"]]:
                                        touched.password && errors.password,
                                }
                            )}
                            placeholder="Начните вводить..."
                        />
                        {touched.password && errors.password && (
                            <span className={css["error-message"]}>
                                {errors.password}
                            </span>
                        )}
                        <a
                            href="#"
                            onClick={(e) => {
                                e.preventDefault();
                                dispatch(setCurrentModal("reset"));
                            }}
                            className={css["form__forget-password"]}
                        >
                            Забыли пароль?
                        </a>
                    </div>
                    {fetchError && (
                        <span className={css["error-message"]}>
                            {fetchError}
                        </span>
                    )}

                    <div className={css["user-modal__buttons"]}>
                        <Button
                            color="outline-blue"
                            className={css["user-modal__button"]}
                            onClick={() => dispatch(setCurrentModal("signup"))}
                        >
                            Создать аккаунт
                        </Button>
                        <Button
                            className={css["user-modal__button"]}
                            disabled={!isFormValid}
                        >
                            Войти
                            <ArrowRightIcon />
                        </Button>
                    </div>
                </form>
            </div>
        </CommonModal>
    );
};

export default LoginModal;
