const mainSlice = createSlice({
    name: "main",
    initialState,
    reducers: {
        setAppIsLoaded(state, action: PayloadAction<boolean>) {
            state.appIsLoaded = action.payload;
        },
        setDays(state, action: PayloadAction<Day[]>) {
            state.days = action.payload;
        },
        completeGame: (
            state,
            action: PayloadAction<{ dayID?: number; gameID?: number }>
        ) => {
            const { dayID, gameID } = action.payload;

            const day = state.days.find((day) => day.id === dayID);
            if (day) {
                const game = day?.games?.find((game) => game.id === gameID);
                if (game) {
                    game.done = true;
                }
            }
        },
        resetGame: (
            state,
            action: PayloadAction<{ dayID?: number; gameID?: number }>
        ) => {
            const { dayID, gameID } = action.payload;

            const day = state.days.find((day) => day.id === dayID);
            if (day) {
                const game = day?.games?.find((game) => game.id === gameID);
                if (game) {
                    game.done = false;
                }
            }
        },
        setCurrentModal: (state, action: PayloadAction<string | null>) => {
            state.currentModal = action.payload;
        },
        setIsLogin: (state, action: PayloadAction<boolean>) => {
            state.isLogin = action.payload;
        },
        setUserEmail: (state, action: PayloadAction<string>) => {
            state.userEmail = action.payload;
        },
    },
});

const { reducer } = mainSlice;
export { reducer as mainReducer };
export const selectAppIsLoaded = (state: RootState) => state.main.appIsLoaded;
export const selectCurrentModal = (state: RootState) => state.main.currentModal;
export const selectDays = (state: RootState) => state.main.days;
export const selectIsLogin = (state: RootState) => state.main.isLogin;
export const selectUserEmail = (state: RootState) => state.main.userEmail;
export const {
    setAppIsLoaded,
    setDays,
    completeGame,
    resetGame,
    setCurrentModal,
    setIsLogin,
    setUserEmail,
} = mainSlice.actions;
