import "swiper/css";
import { Swiper, SwiperSlide } from "swiper/react";
import { EffectCards } from "swiper/modules";
import "swiper/css/effect-cards";
import { Text } from "../../components/Text/Text";
import { Title } from "../../components/Title/Title";
import { useAppDispatch } from "../../store";
import { completeGame, Game } from "../../store/main.reducer";
import css from "./SwiperChoiceGame.module.css";
import classNames from "classnames";
import { useRef, useState } from "react";
import ChevronLeftIcon from "../../icons/ChevronLeftIcon/ChevronLeftIcon";
import ChevronRightIcon from "../../icons/ChevronRightIcon/ChevronRightIcon";
import { Button } from "../../components/Button/Button";
import useIsMobile from "../../hooks/useIsMobile";

export interface SwiperChoiceProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function SwiperChoice({
    game,
    dayID,
    gameID,
    checkGame
}: SwiperChoiceProps) {
    const dispatch = useAppDispatch();
    const swiperRef: any = useRef(null);
    const [currentSlide, setCurrentSlide] = useState(0);
    const isMobile = useIsMobile();

    const slides = [
        {
            id: 1,
            img: "assets/images/days/day1/game5/pic1.png",
            title: "Зарегистрироваться на марафон",
        },
        {
            id: 2,
            img: "assets/images/days/day1/game5/pic2.png",
            title: "Узнать, где учат играть на саксофоне",
        },
        {
            id: 3,
            img: "assets/images/days/day1/game5/pic3.png",
            title: "Пройти 5 минут онлайн-курса, отложенного 3 месяца назад",
        },
    ];

    const submitChoice = () => {
        checkGame(dayID ? dayID : 0, gameID ? gameID : 0);
        // dispatch(completeGame({ dayID: dayID, gameID: gameID }));
    };

    return (
        <div className={css["swiper-choice"]}>
            <Title className={css["swiper-choice__title"]}>
                Сегодня — день начала.
                <br />С чего начнём?
            </Title>
            <div className={css["choice-slider-container"]}>
                {!isMobile && currentSlide !== 0 && (
                    <Button
                        onClick={() => swiperRef.current?.slidePrev()}
                        className={css["cards-slider__prev"]}
                    >
                        <ChevronLeftIcon />
                    </Button>
                )}
                {!isMobile && currentSlide < slides.length && (
                    <Button
                        onClick={() => swiperRef.current?.slideNext()}
                        className={css["cards-slider__next"]}
                    >
                        <ChevronRightIcon />
                    </Button>
                )}

                <div className={css["choice-slider-wrapper"]}>
                    <Swiper
                        className={css["choice-slider"]}
                        slidesPerView={1}
                        onBeforeInit={(swiper: any) => {
                            swiperRef.current = swiper;
                        }}
                        modules={[EffectCards]}
                        grabCursor={true}
                        effect={"cards"}
                        onTransitionEnd={(swiper: any) =>
                            setCurrentSlide(swiper.activeIndex)
                        }
                    >
                        <SwiperSlide>
                            <div
                                className={classNames(
                                    css["choice-slide"],
                                    css["choice-slide_default"]
                                )}
                            >
                                <div
                                    className={
                                        css["choice-slide__default-content"]
                                    }
                                >
                                    <img
                                        src="assets/images/days/day1/game5/pointer.svg"
                                        width={68}
                                        alt=""
                                    />
                                    <Title size="small" color="white">
                                        Листайте карты <br />
                                        и делайте свой выбор
                                    </Title>
                                </div>
                            </div>
                        </SwiperSlide>
                        {slides.map((slide) => {
                            return (
                                <SwiperSlide>
                                    <div className={css["choice-slide"]}>
                                        <div
                                            className={css["choice-slide__pic"]}
                                        >
                                            <img src={slide.img} alt="" />
                                        </div>
                                        <Text
                                            className={
                                                css["choice-slide__title"]
                                            }
                                            color="white"
                                            size="small"
                                        >
                                            {slide.title}
                                        </Text>
                                    </div>
                                </SwiperSlide>
                            );
                        })}
                    </Swiper>
                </div>
            </div>
            <Button
                onClick={submitChoice}
                disabled={currentSlide === 0}
                className={css["swiper-choice__submit"]}
                color="golden"
            >
                Выбрать
            </Button>
        </div>
    );
}
