import { AnimatePresence, motion } from "framer-motion";
import { useEffect } from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import { getProgress } from "./api /user/getProgress";
import ScreenLoader from "./components/ScreenLoader/ScreenLoader";
import { transformProgressData } from "./helpers/transformProgressData";
import { useCookies } from "./hooks/useCookies";
import ModalManager from "./modals";
import DayPage from "./pages/DayPage/DayPage";
import GamePage from "./pages/GamePage/GamePage";
import Main from "./pages/Main/Main";
import { useAppDispatch, useAppSelector } from "./store";
import {
    completeGame,
    selectAppIsLoaded,
    setAppIsLoaded,
    setDays,
    setIsLogin,
    setUserEmail,
} from "./store/main.reducer";

function App() {
    const location = useLocation();
    const dispatch = useAppDispatch();
    const { getCookie } = useCookies();
    const appIsLoaded = useAppSelector(selectAppIsLoaded);

    const pageVariants = {
        initial: {
            opacity: 0,
        },
        in: {
            opacity: 1,
        },
        out: {
            opacity: 0,
        },
    };

    const pageTransition: any = {
        duration: 0.3,
        ease: "easeInOut",
    };

    useEffect(() => {
        const init = async () => {
            try {
                const cookieSessionID = getCookie("sessionID");
                if (cookieSessionID) {
                    const progressResponse = await getProgress(
                        cookieSessionID as string
                    );
                    console.log(progressResponse);
                    dispatch(
                        setDays(
                            transformProgressData(
                                progressResponse?.data?.user_progression
                            )
                        )
                    );
                    dispatch(setIsLogin(true));
                    dispatch(setUserEmail(progressResponse?.data?.user?.email));
                } else {
                    const unloginProgress = getCookie("unlogin_progress");
                    if (unloginProgress && Array.isArray(unloginProgress)) {
                        unloginProgress.forEach((gameID: number) => {
                            dispatch(
                                completeGame({ dayID: 1, gameID: gameID })
                            );
                        });
                    }
                }
            } catch (e) {
                console.log(e);
            } finally {
                dispatch(setAppIsLoaded(true));
            }
        };
        init();
    }, []);

    return (
        <div className="App">
            <ModalManager />
            <AnimatePresence mode="wait">
                {appIsLoaded ? (
                    <Routes location={location} key={location.pathname}>
                        <Route
                            path="/"
                            element={
                                <motion.div
                                    initial="initial"
                                    animate="in"
                                    exit="out"
                                    variants={pageVariants}
                                    transition={pageTransition}
                                >
                                    <Main />
                                </motion.div>
                            }
                        />
                        <Route
                            path="/day"
                            element={
                                <motion.div
                                    initial="initial"
                                    animate="in"
                                    exit="out"
                                    variants={pageVariants}
                                    transition={pageTransition}
                                >
                                    <DayPage />
                                </motion.div>
                            }
                        />
                        <Route
                            path="/game"
                            element={
                                <motion.div
                                    initial="initial"
                                    animate="in"
                                    exit="out"
                                    variants={pageVariants}
                                    transition={pageTransition}
                                >
                                    <GamePage />
                                </motion.div>
                            }
                        />
                    </Routes>
                ) : (
                    <ScreenLoader />
                )}
            </AnimatePresence>
        </div>
    );
}

export default App;
