import { useLocation, useNavigate } from "react-router-dom";
import "swiper/css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Button } from "../../components/Button/Button";
import Footer from "../../components/Footer/Footer";
import Header from "../../components/Header/Header";
import { Title } from "../../components/Title/Title";
import ProfileIcon from "../../icons/ProfileIcon/ProfileIcon";
import { useAppDispatch, useAppSelector } from "../../store";
import {
    selectDays,
    selectIsLogin,
    setCurrentModal,
} from "../../store/main.reducer";
import css from "./DayPage.module.css";
import { Text } from "../../components/Text/Text";
import PlusIcon from "../../icons/PlusIcon/PlusIcon";

export interface DayPageProps {}

export default function DayPage(props: DayPageProps) {
    const { state } = useLocation();
    const days = useAppSelector(selectDays);
    const { dayID } = state;
    const currentDay = days.find((day) => day.id === dayID);
    const isPenalty = currentDay?.penalty;
    const dayGames = currentDay?.games ? [...currentDay.games.slice(0, 5)] : [];
    const finishedGames = dayGames?.filter((day) => day.done).length;
    const isLogin = useAppSelector(selectIsLogin);
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const dayIsDone = finishedGames === 5;

    return (
        <div className={css["main"]}>
            <Header />
            <main className={css["main-content"]}>
                <div className="container">
                    <div className={css["day-wrapper"]}>
                        <header className={css["day-header"]}>
                            <div className={css["day-header__day"]}>
                                День {dayID}
                            </div>
                            <Title
                                className={css["day-header__title"]}
                                color="white"
                            >
                                {currentDay?.name}
                            </Title>
                            {!isPenalty ? (
                                <div className={css["day-header__counter"]}>
                                    {finishedGames}/{dayGames?.length}
                                </div>
                            ) : (
                                <div></div>
                            )}
                        </header>
                        {dayIsDone ? (
                            <div className={css["day-is-done"]}>
                                <div className={css["day-is-done__pic"]}>
                                    <div
                                        className={
                                            css["day-is-done__pic-content"]
                                        }
                                    >
                                        <PlusIcon
                                            fill="#C38500"
                                            width={31}
                                            height={31}
                                        />
                                        <img
                                            width={60}
                                            src={currentDay?.icon}
                                            alt=""
                                        />
                                    </div>
                                </div>
                                <Title>
                                    Вы завершили
                                    <br />
                                    все задачи этого дня!
                                </Title>
                                <Button
                                    color="golden"
                                    className={css["day-is-done__button"]}
                                    onClick={() => navigate("/")}
                                >
                                    К списку дней
                                </Button>
                            </div>
                        ) : isPenalty ? (
                            <div className={css["penalty-block"]}>
                                <img
                                    src="assets/images/penalty-pic.png"
                                    width={160}
                                    alt=""
                                />
                                <Title>Вам штраф!</Title>
                                <Text>
                                    Чтобы открыть доступ к заданиям и получить
                                    нужный ингредиент, закройте свой штраф от
                                    Завтра
                                </Text>
                                <Button
                                    color="blue"
                                    className={css["penalty-block__button"]}
                                    onClick={() =>
                                        dispatch(setCurrentModal("penalty"))
                                    }
                                >
                                    Выполнить
                                </Button>
                            </div>
                        ) : (
                            <div className={css["day-items"]}>
                                <Swiper
                                    slidesPerView={"auto"}
                                    spaceBetween={20}
                                    className={css["day-swiper"]}
                                >
                                    {dayGames?.map((game, gameIndex) => {
                                        return (
                                            <SwiperSlide>
                                                <div
                                                    className={css["day-item"]}
                                                    key={game?.id}
                                                >
                                                    <div
                                                        className={
                                                            css[
                                                                "day-item__content"
                                                            ]
                                                        }
                                                    >
                                                        <div
                                                            className={
                                                                css[
                                                                    "day-item__pic-wrapper"
                                                                ]
                                                            }
                                                        >
                                                            <div
                                                                className={
                                                                    css[
                                                                        "day-item__pic"
                                                                    ]
                                                                }
                                                            >
                                                                <img
                                                                    src={
                                                                        game.pic
                                                                    }
                                                                    width={160}
                                                                    alt=""
                                                                />
                                                            </div>
                                                        </div>
                                                        <Title
                                                            className={
                                                                css[
                                                                    "day-item__title"
                                                                ]
                                                            }
                                                            size="small"
                                                        >
                                                            {game.title}
                                                        </Title>
                                                        {!isLogin &&
                                                        gameIndex > 2 ? (
                                                            <Button
                                                                className={
                                                                    css[
                                                                        "day-item__login-button"
                                                                    ]
                                                                }
                                                                color={"white"}
                                                                onClick={() =>
                                                                    dispatch(
                                                                        setCurrentModal(
                                                                            "login"
                                                                        )
                                                                    )
                                                                }
                                                            >
                                                                <ProfileIcon fill="#35A2EB" />
                                                                <span>
                                                                    Войти
                                                                </span>
                                                            </Button>
                                                        ) : (
                                                            <Button
                                                                className={
                                                                    css[
                                                                        "day-item__button"
                                                                    ]
                                                                }
                                                                color={
                                                                    game.done
                                                                        ? "golden"
                                                                        : "orange"
                                                                }
                                                                onClick={() =>
                                                                    navigate(
                                                                        "/game",
                                                                        {
                                                                            state: {
                                                                                dayID: currentDay?.id,
                                                                                gameID: game?.id,
                                                                            },
                                                                        }
                                                                    )
                                                                }
                                                            >
                                                                {game.done
                                                                    ? "Выполнено"
                                                                    : "Играть"}
                                                            </Button>
                                                        )}
                                                    </div>
                                                </div>
                                            </SwiperSlide>
                                        );
                                    })}
                                </Swiper>
                            </div>
                        )}
                    </div>
                </div>
            </main>
            <Footer />
        </div>
    );
}
