import classNames from "classnames";
import { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Text } from "../../components/Text/Text";
import { Title } from "../../components/Title/Title";
import CheckmarkIcon from "../../icons/CheckmarkIcon/CheckmarkIcon";
import { useAppDispatch } from "../../store";
import { completeGame, Game } from "../../store/main.reducer";
import css from "./CheckListGame.module.css";
import { postGame } from "../../api /user/postGame";

export interface CheckListGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function CheckListGame({
    game,
    dayID,
    gameID,
    checkGame
}: CheckListGameProps) {
    const dispatch = useAppDispatch();
    const [answers, setAnswers] = useState<number[]>([]);

    const submitAnswer = async () => {
        checkGame(dayID ? dayID : 0, gameID ? gameID : 0);
        // dispatch(completeGame({ dayID: dayID, gameID: gameID }));
    };

    const gameContent = {
        title: "Сегодняшний список маленьких достижений",
        list: [
            {
                id: 1,
                text: "Открыли глаза и не закрыли их обратно",
            },
            {
                id: 2,
                text: "Достали ежедневник (пусть даже ещё и не открывали)",
            },
            {
                id: 3,
                text: "Вспомнили, что хотели начать",
            },
        ],
    };

    const toggleAnswer = (answerID: number) => {
        if (answers.includes(answerID)) {
            setAnswers([...answers.filter((answer) => answer !== answerID)]);
        } else {
            setAnswers([...answers, answerID]);
        }
    };


    return (
        <div className={css["check-list"]}>
            <Title className={css["check-list__title"]}>
                {gameContent.title}
            </Title>
            <div className={css["check-list__answers"]}>
                {gameContent.list.map((answer) => (
                    <div
                        key={answer.id}
                        onClick={() => toggleAnswer(answer.id)}
                        className={css["check-list-answer"]}
                    >
                        <div
                            className={classNames(
                                css["check-list-answer__content"],
                                answers.includes(answer.id) &&
                                    css["check-list-answer__content_active"]
                            )}
                        >
                            <div
                                className={classNames(
                                    css["check-list-answer__checkmark"],
                                    answers.includes(answer.id) &&
                                        css[
                                            "check-list-answer__checkmark_active"
                                        ]
                                )}
                            >
                                {answers.includes(answer.id) && (
                                    <CheckmarkIcon />
                                )}
                            </div>
                            <Text
                                color={
                                    answers.includes(answer.id)
                                        ? "black"
                                        : "white"
                                }
                                className={css["check-list-answer__text"]}
                            >
                                {answer.text}
                            </Text>
                        </div>
                    </div>
                ))}
            </div>
            <Button
                className={css["check-list__submit"]}
                color="golden"
                disabled={answers.length === 0}
                onClick={submitAnswer}
            >
                Далее
            </Button>
        </div>
    );
}
