import { useState, useCallback, useMemo } from "react";
// import { FormData, FormErrors } from "./types";s

export interface FormData {
    email: string;
    password: string;
    confirmPassword: string;
    rules: boolean;
    conditions: boolean;
    mailing: boolean;
}

export interface FormErrors {
    email?: string;
    password?: string;
    confirmPassword?: string;
    rules?: string;
    conditions?: string;
}

export const useRegistrationForm = () => {
    const [formData, setFormData] = useState<FormData>({
        email: "",
        password: "",
        confirmPassword: "",
        rules: false,
        conditions: false,
        mailing: false,
    });

    const [errors, setErrors] = useState<FormErrors>({});
    const [touched, setTouched] = useState<Record<string, boolean>>({});

    const validateField = useCallback(
        (name: string, value: any): string => {
            switch (name) {
                case "email":
                    if (!value) return "Email обязателен";
                    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value))
                        return "Неверный формат email";
                    return "";

                case "password":
                    if (!value) return "Пароль обязателен";
                    if (value.length < 6)
                        return "Пароль должен содержать минимум 6 символов";
                    if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(value))
                        return "Пароль должен содержать буквы в верхнем и нижнем регистре и цифры";
                    return "";

                case "confirmPassword":
                    if (!value) return "Подтверждение пароля обязательно";
                    if (value !== formData.password)
                        return "Пароли не совпадают";
                    return "";

                case "rules":
                    if (!value) return "Необходимо согласие с правилами акции";
                    return "";

                case "conditions":
                    if (!value)
                        return "Необходимо согласие с условиями конфиденциальности";
                    return "";

                default:
                    return "";
            }
        },
        [formData.password]
    );

    // Мемоизированная проверка валидности формы
    const isFormValid = useMemo(() => {
        return Object.keys(formData).every((key) => {
            if (key === "mailing") return true; // mailing не обязателен

            const error = validateField(key, formData[key as keyof FormData]);
            return !error;
        });
    }, [formData, validateField]);

    const validateForm = useCallback((): boolean => {
        const newErrors: FormErrors = {};

        Object.keys(formData).forEach((key) => {
            if (key !== "mailing") {
                const error = validateField(
                    key,
                    formData[key as keyof FormData]
                );
                if (error) {
                    newErrors[key as keyof FormErrors] = error;
                }
            }
        });

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    }, [formData, validateField]);

    const handleChange = useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            const { name, value, type, checked } = e.target;

            setFormData((prev) => ({
                ...prev,
                [name]: type === "checkbox" ? checked : value,
            }));

            if (touched[name]) {
                const error = validateField(
                    name,
                    type === "checkbox" ? checked : value
                );
                setErrors((prev) => ({ ...prev, [name]: error }));
            }
        },
        [touched, validateField]
    );

    const handleBlur = useCallback(
        (e: React.FocusEvent<HTMLInputElement>) => {
            const { name, value, type, checked } = e.target;

            setTouched((prev) => ({ ...prev, [name]: true }));

            const error = validateField(
                name,
                type === "checkbox" ? checked : value
            );
            setErrors((prev) => ({ ...prev, [name]: error }));
        },
        [validateField]
    );

    const handleSubmit = useCallback(
        (e: React.FormEvent) => {
            e.preventDefault();

            const allTouched = Object.keys(formData).reduce((acc, key) => {
                acc[key] = true;
                return acc;
            }, {} as Record<string, boolean>);

            setTouched(allTouched);

            if (validateForm()) {
                console.log("Форма валидна, данные:", formData);
                // Логика отправки формы
            }
        },
        [formData, validateForm]
    );

    return {
        formData,
        errors,
        touched,
        isFormValid,
        handleChange,
        handleBlur,
        handleSubmit,
        validateForm,
    };
};
