import classNames from "classnames";
import { useNavigate } from "react-router-dom";
import { Button } from "../../components/Button/Button";
import Footer from "../../components/Footer/Footer";
import Header from "../../components/Header/Header";
import ProgressSvg from "../../components/ProgressSvg/ProgressSvg";
import { Text } from "../../components/Text/Text";
import Timer from "../../components/Timer/Timer";
import CheckmarkIcon from "../../icons/CheckmarkIcon/CheckmarkIcon";
import PlusIcon from "../../icons/PlusIcon/PlusIcon";
import ProfileTimerIcon from "../../icons/TimerIcon/TimerIcon";
import { useAppDispatch, useAppSelector } from "../../store";
import {
    selectDays,
    selectIsLogin,
    setCurrentModal,
} from "../../store/main.reducer";
import css from "./Main.module.css";
import { Swiper, SwiperSlide } from "swiper/react";
import useIsMobile from "../../hooks/useIsMobile";
import "swiper/css";
import { Title } from "../../components/Title/Title";
import ProfileIcon from "../../icons/ProfileIcon/ProfileIcon";

export interface MainProps {}

export default function Main(props: MainProps) {
    const days = useAppSelector(selectDays);
    const navigate = useNavigate();
    const isMobile = useIsMobile();
    const isLogin = useAppSelector(selectIsLogin);
    const dispatch = useAppDispatch();

    const activeIndexes = days
        .map((day, index) => (day.active ? index : -1))
        .filter((index) => index !== -1);

    const currentDay =
        activeIndexes.length > 0 ? activeIndexes[activeIndexes.length - 1] : -1;

    const daysList = days.map((day, dayIndex) => {
        const mainDay = (
            <div className={css["main-day-wrapper"]} key={day.id}>
                <div className={css["main-day"]}>
                    <div className={css["main-day__pic"]}>
                        <img width={70} height={70} src={day.pic} alt="" />
                    </div>
                    <div
                        className={classNames(
                            css["day-info"],
                            day.active && css["day-info_active"]
                        )}
                    >
                        <div
                            className={classNames(
                                css["day-info__check"],
                                day.done && css["day-info__check_active"]
                            )}
                        >
                            {day.done && <CheckmarkIcon />}
                        </div>
                        <Text
                            size="normal"
                            className={css["day-info__text"]}
                            color={!day.active ? "white" : "black"}
                        >
                            День {day.id}
                            <span
                                style={{
                                    color: day.active
                                        ? "#00000099"
                                        : "#FFFFFF99",
                                }}
                            >
                                {" "}
                                – {day.name}
                            </span>
                        </Text>
                        {isLogin && day.penalty && (
                            <Button
                                color="blue"
                                className={css["main-day__button"]}
                                onClick={() =>
                                    navigate("/day", {
                                        state: {
                                            dayID: day.id,
                                        },
                                    })
                                }
                            >
                                Штраф
                            </Button>
                        )}
                        {isLogin && !day.active && !day.penalty && (
                            <div className={css["day-timer"]}>
                                <ProfileTimerIcon fill="#FFFFFF88" />
                                <span className={css["day-timer__text"]}>
                                    До открытия:
                                </span>
                                <span className={css["day-timer__time"]}>
                                    <Timer time={day.time} />
                                </span>
                            </div>
                        )}
                    </div>
                    {!isLogin && dayIndex > 0 && (
                        <Button
                            className={classNames(
                                css["main-day__button"],
                                css["login-button"]
                            )}
                            color="transparent"
                            onClick={() => dispatch(setCurrentModal("login"))}
                        >
                            <ProfileIcon fill="#fff" />
                            <Text>Войти</Text>
                        </Button>
                    )}
                    {day.active && !day.done && (
                        <Button
                            className={classNames(
                                css["main-day__button"],
                                css["to-task-button"]
                            )}
                            color="golden"
                            onClick={() =>
                                navigate("/day", {
                                    state: {
                                        dayID: day.id,
                                    },
                                })
                            }
                        >
                            <Text>К заданиям</Text>
                            <div className={css["to-task-button__icon"]}>
                                <PlusIcon fill="#C38500" />
                                <img width={21} src={day.icon} alt="" />
                            </div>
                        </Button>
                    )}
                    {day.active && day.done && (
                        <Button
                            className={classNames(
                                css["main-day__button"],
                                css["task-done-button"]
                            )}
                            color="blue"
                        >
                            <img src={day.icon} width={35} alt="" />
                            <Text>Выполнено</Text>
                        </Button>
                    )}
                </div>
            </div>
        );

        return isMobile ? <SwiperSlide>{mainDay}</SwiperSlide> : mainDay;
    });

    return (
        <div className={css["main"]}>
            <Header>
                <Title className={css["main__title"]} align="center">
                    5 дней по 5 заданий.
                    <br />1 цель — дойти до конца
                </Title>
            </Header>
            <main className={css["main-content"]}>
                <div className="container">
                    <div className={css["main-wrapper"]}>
                        <div className={css["main-days"]}>
                            {isMobile ? (
                                <Swiper
                                    slidesPerView={"auto"}
                                    spaceBetween={20}
                                >
                                    {daysList}
                                </Swiper>
                            ) : (
                                daysList
                            )}
                        </div>
                        <div
                            className={classNames(
                                css["progress-block"],
                                css[`progress-block_day_${currentDay}`]
                            )}
                        >
                            <img
                                className={css["progress-block__logo"]}
                                width={183}
                                height={140}
                                src="assets/images/maxi-king-logo.png"
                                alt=""
                            />
                            <div className={css["progress-block__image"]}></div>
                            <div className={css["progress-bar"]}>
                                <div className={css["progress-bar__line"]}>
                                    <ProgressSvg currentDay={currentDay} />
                                </div>
                                {days.map((day) => {
                                    return (
                                        <div
                                            className={classNames(
                                                css["progress-bar__item"],
                                                day.active &&
                                                    css[
                                                        "progress-bar__item_active"
                                                    ],
                                                css[
                                                    `progress-bar__item_type_${day.type}`
                                                ]
                                            )}
                                            key={day.id}
                                        >
                                            <img
                                                src={day.icon}
                                                alt=""
                                                width={35}
                                            />
                                        </div>
                                    );
                                })}

                                {/* <div
                                    className={classNames(
                                        css["progress-bar__item"],
                                        css["progress-bar__item_active"],
                                        css["progress-bar__item_type_nut"]
                                    )}
                                >
                                    <img
                                        src="assets/images/nut-icon.png"
                                        alt=""
                                        width={35}
                                    />
                                </div>
                                <div
                                    className={classNames(
                                        css["progress-bar__item"],
                                        css["progress-bar__item_active"],
                                        css["progress-bar__item_type_waffle"]
                                    )}
                                >
                                    <img
                                        src="assets/images/waffle-icon.png"
                                        alt=""
                                        width={35}
                                    />
                                </div>
                                <div
                                    className={classNames(
                                        css["progress-bar__item"],
                                        css["progress-bar__item_active"],
                                        css["progress-bar__item_type_milk"]
                                    )}
                                >
                                    <img
                                        src="assets/images/milk-icon.png"
                                        alt=""
                                        width={35}
                                    />
                                </div>
                                <div
                                    className={classNames(
                                        css["progress-bar__item"],
                                        css["progress-bar__item_active"],
                                        css["progress-bar__item_type_caramel"]
                                    )}
                                >
                                    <img
                                        src="assets/images/caramel-icon.png"
                                        alt=""
                                        width={35}
                                    />
                                </div> */}
                            </div>
                        </div>
                    </div>
                </div>
            </main>
            <Footer />
        </div>
    );
}
