import { useNavigate } from "react-router-dom";
import { Game } from "../../store/main.reducer";
import { Button } from "../Button/Button";
import { Text } from "../Text/Text";
import { Title } from "../Title/Title";
import css from "./GameDone.module.css";

export interface GameDoneProps {
    game?: Game;
}

export default function GameDone({ game }: GameDoneProps) {

    const navigate = useNavigate()

    return (
        <div className={css["game-done"]}>
            <img
                className={css["game-done__image"]}
                src={game?.complete.pic}
                width={190}
                alt=""
            />
            <Title className={css["game-done__title"]} color="white">
                {game?.complete.title}
            </Title>
            <Text className={css["game-done__text"]} color="white" >
                {game?.complete.text}
            </Text>
            <Button className={css["game-done__button"]} color="golden" onClick={()=>navigate(-1)}>
                Вернуться к играм
            </Button>
            <Button className={css["game-done__button"]} color="outline">
                Повторить викторину
            </Button>
        </div>
    );
}
